import React from "react";
import { faMapMarkerAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export function Location(props) {
    return(
        <React.Fragment>
            <div className="flex flex-col space-y-5">
                    <div onClick={()=>{
                        props.setHelski(true)
                        props.setTurku(false)
                        props.setOulu(false)
                        props.setVaasa(false)
                    }} className="flex space-x-4 cursor-pointer w-96">
                        <FontAwesomeIcon className="text-gray-500 text-2xl" icon={faMapMarkerAlt} />
                        <p className="text-gray-500 ">Helski, Finland</p>
                    </div>
                    <div onClick={()=>{
                        props.setHelski(false)
                        props.setTurku(true)
                        props.setOulu(false)
                        props.setVaasa(false)
                    }} className="flex space-x-4 cursor-pointer w-96">
                        <FontAwesomeIcon className="text-gray-500 text-2xl" icon={faMapMarkerAlt} />
                        <p className="text-gray-500 ">Turku, Finland</p>
                    </div>
                    <div onClick={()=>{
                        props.setHelski(false)
                        props.setTurku(false)
                        props.setOulu(true)
                        props.setVaasa(false)
                    }} className="flex space-x-4 cursor-pointer w-96">
                        <FontAwesomeIcon className="text-gray-500 text-2xl" icon={faMapMarkerAlt} />
                        <p className="text-gray-500 ">Oulu, Finland</p>
                    </div>
                    <div onClick={()=>{
                        props.setHelski(false)
                        props.setTurku(false)
                        props.setOulu(false)
                        props.setVaasa(true)
                    }} className="flex space-x-4 cursor-pointer w-96">
                        <FontAwesomeIcon className="text-gray-500 text-2xl" icon={faMapMarkerAlt} />
                        <p className="text-gray-500 ">Vaasa, Finland</p>
                    </div>
                </div>
        </React.Fragment>
    )
}