import React from "react";
import { useState } from "react/cjs/react.development";

export default function Guest(props) {
  return (
    <React.Fragment>
      <div className="" style={{ marginLeft: "330px" }}>
        <div className="flex flex-col space-y-3">
          <p className="font-medium">Adults</p>
          <p className="text-gray-500">Ages 13 or above</p>
          <div className="flex items-center space-x-5">
            <button
              onClick={() => {
                if (props.guest === 0) {
                  return;
                } else {
                  props.setGuest(props.guest - 1);
                }
              }}
              className="border border-gray-500 rounded-lg py-1 px-4 text-xl"
            >
              -
            </button>
            <p>{props.guest}</p>
            <button
              onClick={() => {
                props.setGuest(props.guest + 1);
              }}
              className="border border-gray-500 rounded-lg py-1 px-4 text-xl"
            >
              +
            </button>
          </div>
        </div>

        <div className="flex flex-col space-y-3">
          <p className="font-medium">Children</p>
          <p className="text-gray-500">Ages 2 - 12</p>
          <div className="flex items-center space-x-5">
            <button
              onClick={() => {
                if (props.children === 0) {
                  return;
                } else {
                  props.setChildren(props.children - 1);
                }
              }}
              className="border border-gray-500 rounded-lg py-1 px-4 text-xl"
            >
              -
            </button>
            <p>{props.children}</p>
            <button
              onClick={() => {
                props.setChildren(props.children + 1);
              }}
              className="border border-gray-500 rounded-lg py-1 px-4 text-xl"
            >
              +
            </button>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
