import React from "react";

export default function Body(props) {
  return (
    <React.Fragment>
        <div className="">
          <div
            style={{
              backgroundImage: `url(${props.url})`,
            }}
            className="bg-red-500 bg-cover h-80 rounded-3xl"
          ></div>
          <div className="py-4">
            <div className="flex space-x-4 items-center justify-between">
              <div className="flex space-x-4 items-center">
                {props.superHost?(
                    <React.Fragment>
                        <p className="border border-black px-2 py-1 rounded-xl uppercase font-semibold">
                  super host
                </p>
                    </React.Fragment>
                ):""}
                <p className="text-gray-500">{props.type}.{""} {props.beds} beds</p>
              </div>
              <div className="flex space-x-3">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-5 w-5 text-red-600"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                </svg>
                <p>{props.rating}</p>
              </div>
            </div>
            <p className="font-semibold mt-3">{props.title}</p>
          </div>
        </div>
    </React.Fragment>
  );
}
