import React from "react";
import logo from "../assets/logo.png";

export default function Header(props) {
  let place = "helski"
  if (props.helski) {
    place = "Helski";
  } else if (props.turku) {
    place = "Turku";
  } else if (props.oulu) {
    place = "Oulu";
  } else {
    place = "Vaasa";
  }
  return (
    <React.Fragment>
      <div onClick={()=>{props.setSearch(true)}} className="flex items-center justify-between">
        <img src={logo} alt="" className="" />
        <div className="flex cursor-pointer bg-white shadow-lg rounded-lg space-x-4 pr-2">
          <p className="text-lg capitalize border px-4 border-t-0 border-b-0 py-2 border-l-0">{place}, finland</p>
          <p className="text-lg border px-4 text-gray-400 border-t-0 border-b-0 py-2 border-l-0">{props.adult == 0&&props.children == 0?"Add":props.adult+props.children} guest</p>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-10 w-10 py-2 text-red-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
        </div>
      </div>
    </React.Fragment>
  );
}
