import React, { useState } from "react";
import Guest from "./guest";
import { Location } from "./location";

export default function SearchPage(props) {
  const [location, setLocation] = useState(true);
 
  let place = "Helski";
  if (props.helski) {
    place = "Helski";
  } else if (props.turku) {
    place = "Turku";
  } else if (props.oulu) {
    place = "Oulu";
  } else {
    place = "Vaasa";
  }
  return (
    <React.Fragment>
      <div className="absolute inset-0 bg-opacity-30 flex flex-col">
        <div
          style={{ height: "400px" }}
          className="bg-white w-full flex px-10 space-y-8 flex-col py-20"
        >
          <div className="flex border rounded-xl bg-white shadow-md">
            <div
              onClick={() => {
                setLocation(true);
              }}
              className="py-2 pl-5 border border-l-0 cursor-pointer border-t-0 border-b-0 pr-80"
            >
              <p className="font-semibold">Location</p>
              <p className="">{place}, Finland</p>
            </div>
            <div
              onClick={() => {
                setLocation(false);
              }}
              className=" py-2 pl-5 border border-l-0 cursor-pointer border-t-0 border-b-0 pr-80"
            >
              <p className="font-semibold">Guest</p>
              <p className=" text-gray-400">
                {props.adult == 0 && props.children == 0 ? "Add guest" : props.adult + props.children}
              </p>
            </div>
            <div className="pr-80 flex items-center justify-center px-56">
              <div onClick={()=>{props.setSearch(false)}} className="bg-red-600 cursor-pointer shadow-md flex py-3 px-4 rounded-xl space-x-3">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="text-white h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
                <p className="text-white ">Search</p>
              </div>
            </div>
          </div>
          <div className="px-10">
            {location ? (
              <React.Fragment>
                <Location
                  helski={props.helski}
                  setHelski={props.setHelski}
                  turku={props.turku}
                  setTurku={props.setTurku}
                  oulu={props.oulu}
                  setOulu={props.setOulu}
                  vaasa={props.vaasa}
                  setVaasa={props.setVaasa}
                ></Location>
              </React.Fragment>
            ) : (
              <React.Fragment>
                <Guest
                  guest={props.adult}
                  children={props.children}
                  setChildren={props.setChildren}
                  setGuest={props.setAdult}
                ></Guest>
              </React.Fragment>
            )}
          </div>
        </div>
      <div
      onClick={()=>{props.setSearch(false)}}
        className="bg-black bg-opacity-30 relative inset-0 h-full"
      ></div>
      </div>
    </React.Fragment>
  );
}
